@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.card-front {
  width: 300px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.card-front img {
  width: 85%;
}

.word-search {
  width: 400px;
  padding: 5px;
}

.card-container {
  width: 300px;
  height: 350px;
  background: #56afb3;
  border-radius: 50px;
  border: 5px solid #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  margin: 20px;
}

.card-back {
  width: 300px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.card-back h2, p {
  color: #fff;
}

.card-back p {
  text-align: center;
  font-size: 20px;
}

.header {
  width: 100%;
  height: 50px;
  /* line-height: 100px; */
  display: inline-block;
  text-align: center;
  font-size: 50px;
}

.footer {
  width: 100%;
  height: 50px;
  text-align: center;
}
